<script setup lang="ts">
import { storeToRefs } from 'pinia'
import { dropHandlers, type DropResult, smoothDnD } from 'smooth-dnd'
import { toRaw } from 'vue'

import { SmoothDndContainer } from '@/components/SmoothDnd/SmoothDndContainer'
import { SmoothDndDraggable } from '@/components/SmoothDnd/SmoothDndDraggable'
import { useAppEditorStore } from '@/stores/appEditor'
import { arrayMove } from '@/utils/array'

import BlockRenderer from './BlockRenderer.vue'

smoothDnD.dropHandler = dropHandlers.reactDropHandler().handler

const appEditorStore = useAppEditorStore()

// storeÂÜÖÈÉ®ÁöÑÊï∞ÊçÆÊòØÂìçÂ∫îÂºèÁöÑÔºåÂ¶ÇÊûúÁõ¥Êé•Ëß£ÊûÑÔºåÂìçÂ∫îÂºè‰ºö‰∏¢Â§±
const { blocks } = storeToRefs(appEditorStore)
const { updateBlocks } = appEditorStore

/**
 * 
 * ËøîÂõûnewBlocks
 * @param arr ÂéüblocksÈõÜ
 * @param dragResult ÊãñÊãΩÁªìÊûú‰ø°ÊÅØ
 */
const applyDrag = <T extends any[]>(arr: T, dragResult: DropResult) => {
  
    const { removedIndex, addedIndex, payload } = dragResult

    const result = [...arr]

    //ÊÉÖÂÜµ1ÔºöÊãñÂá∫ÂÆπÂô®->‰∏çÂ§ÑÁêÜ
    // addedIndex: ÊãñÂä®ÂÖÉÁ¥†Ë¢´ÊîæÂÖ•Êñ∞ÂÆπÂô®ÁöÑ‰ΩçÁΩÆÁ¥¢ÂºïÔºåÂ¶ÇÊûúÊòØÊãñÂá∫ÂÆπÂô®ÔºåËøôÈáå‰∏∫null
    if (addedIndex === null) return result

    //ÊÉÖÂÜµ2Ôºö‰ªéÂ§ñÈÉ®ÊãñÂÖ•ËøõÂÆπÂô® -> ÊèíÂÖ•Êñ∞ÂÖÉÁ¥†
    // removedIndex: Ë¢´ÊãñÂä®ÂÖÉÁ¥†Âú®ÂéüÊï∞ÁªÑ‰∏≠ÁöÑÁ¥¢Âºï„ÄÇÂ¶ÇÊûúÊòØ‰ªéÂ§ñÈÉ®ÂÆπÂô®ÊãñÂÖ•ÔºåËøôÈáå‰∏∫null
    if (addedIndex !== null && removedIndex === null) {
        result.splice(addedIndex, 0, {
            id: `${Math.random()}`,
            ...payload //ÊãñÂä®Êï∞ÊçÆËΩΩËç∑
        })
    }

    // ÊÉÖÂÜµ3ÔºöÂÆπÂô®ÂÜÖÈÉ®ÁöÑÁßªÂä® -> Âà†Èô§ÂéüÊù•‰ΩçÁΩÆÔºåÊ∑ªÂä†Êñ∞‰ΩçÁΩÆ
    if (addedIndex !== null && removedIndex !== null) {
        return arrayMove(result, removedIndex, addedIndex)
    }

    return result
}

</script>

<template>
    <smooth-dnd-container
        drag-handle-selector=".handle"
        group-name="blocks"
        orientation="vertical"
        tag="div"
        class="renderer-dnd-container"
        @drop="updateBlocks(applyDrag(toRaw(blocks), $event))"
    >
        <smooth-dnd-draggable v-for="(block, i) in blocks" :key="block.id">
            <BlockRenderer :block="block" :i="i" />
        </smooth-dnd-draggable>
    </smooth-dnd-container>
</template>

<style scoped>
.renderer-dnd-container {
    width: 100%;
}
</style>

<style lang="css">
.smooth-dnd-draggable-wrapper {
    overflow: visible !important;
}
</style>
